<template>
	<div id="order-note">
		<public-header title="添加备注"></public-header>
		<div class="content">
			<template v-if="type == 'note'">
				<van-cell-group class="text">
					<van-field v-model="note" type="textarea" placeholder="请输入备注，最多50字哦"/>
					<div class="label-box" v-if="(order_note && order_note.length > 0) || takeoutOrderNote.length > 0">
						<div class="flex-lr font-12 c-gray margin-10-b">
							<div>快捷标签</div>
							<template v-if="takeoutOrderNote.length > 0 && !editStatus">
								<div class="flex"  @click="editStatus = true">
									<i class="icon icon-edit"></i>
									<span class="margin-5-l">编辑</span>
								</div>
							</template>
							<template v-if="editStatus">
								<div class="flex"  @click="onSaveNote">
									<i class="icon icon-roundcheck"></i>
									<span class="margin-5-l">保存</span>
								</div>
							</template>
						</div>
						<template v-if="takeoutOrderNote.length > 0">
							<div class="label" @click="onChooseLabel(item)" v-for="(item, index) in takeoutOrderNote" :key="index">
								{{item}}
								<i class="icon icon-close" @click.stop="onDeleteNote(index)" v-if="editStatus"></i>
							</div>
						</template>
						<div class="label" v-for="item in order_note" @click="onChooseLabel(item)">{{item}}</div>
					</div>
				</van-cell-group>
			</template>
			<template v-else-if="type == 'invoice'">
				<van-radio-group v-model="radio">
					<van-cell-group>
						<van-cell title="发票"></van-cell>
						<van-cell title="不需要发票" clickable @click="radio = 0">
							<template #right-icon>
								<van-radio :name="0" />
							</template>
						</van-cell>
						<van-cell :title="item.title" clickable @click="radio = item.id; invoiceTitle = item.title" v-for="(item, index) in invoices" :key="index">
							<template #right-icon>
								<van-radio :name="item.id" />
							</template>
						</van-cell>
					</van-cell-group>
				</van-radio-group>
				<div class="add-invoice" @click="util.jsUrl('/core/pages/order/invoice', query, 'replace')"><i class="icon icon-roundadd"></i>添加发票抬头</div>
			</template>
			<div class="save-btn">
				<van-button type="danger" size="large" @click="onSubmit">保存</van-button>
			</div>
		</div>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from '@/components/header'
	export default {
		name: "orderNote",
		components: {
			PublicHeader
		},
		data() {
			return {
				query: {},
				sids: '',
				note: '',
				sid: Number,
				order_note: [],
				takeoutOrderNote: [],
				editStatus: false,
				invoices: [],
				radio: 0,
				type: 'note',
				channel: '',
				showPreLoading: true,
				invoiceTitle: '',
				title: '添加备注',
			}
		},
		methods: {
			...mapMutations([
				'setOrderExtra', 'setMallOrderExtra'
			]),

			onSaveNote() {
				this.util.setStorage('takeoutOrderNote', this.takeoutOrderNote);
				this.util.$toast('标签保存成功');
				this.editStatus = false;
			},

			onDeleteNote(index) {
				this.$delete(this.takeoutOrderNote, index);
			},

			onLoad() {
				this.util.request({
					url: 'wmall/order/create/note',
					data: {
						sid: this.sid,
						type: this.type
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					result = result.message;
					if(this.type == 'note') {
						var takeoutOrderNote = this.util.getStorage('takeoutOrderNote');
						if(takeoutOrderNote) {
							this.takeoutOrderNote = takeoutOrderNote;
						}
						this.order_note = [...this.order_note, ...result.store.order_note];
					} else if(this.type == 'invoice') {
						this.util.setWXTitle('添加发票');
						this.title = '添加发票';
						if(result.store.invoice_status == 1) {
							this.invoices = [...this.invoices, ...result.invoices]
						} else {
							if(this.channel == 'mall') {
								this.util.$toast('该门店不支持开发票', this.util.getUrl({path: '/mall/pages/order/create', query: {}}), 1000, 'replace');
							} else {
								this.util.$toast('该门店不支持开发票', this.util.getUrl({path: '/pages/order/create', query: {sid: this.sid}}), 1000, 'replace');
							}
						}
					}
				})
			},
			onChooseLabel(note) {
				if(this.editStatus) {
					this.util.$toast('请先保存标签');
					return false;
				}
				this.note = this.note + ' ' + note;
			},
			onSubmit() {
				var that = this;
				if(that.type == 'note' && that.editStatus) {
					that.$dialog.confirm({
						message: '还未保存标签，是否保存'
					}).then(() => {
						that.onSaveNote();
						that.onSubmitPost();
					}).catch(() => {
						that.onSubmitPost();
					})
				} else {
					that.onSubmitPost();
				}
			},
			onSubmitPost() {
				if(this.channel == 'mall') {
					if (this.type == 'note') {
						this.setMallOrderExtra({sid: this.sid, key: 'note', val: this.note});
					} else if(this.type == 'invoice') {
						if (this.radio > 0) {
							this.setMallOrderExtra({sid: this.sid, key: 'invoiceId', val: this.radio});
							this.setMallOrderExtra({sid: this.sid, key: 'invoiceTitle', val: this.invoiceTitle});
						}
					}
					this.$router.replace(this.util.getUrl({path: '/mall/pages/order/create', query: {sids: this.sids}}));
				} else {
					if (this.type == 'note') {
						this.setOrderExtra({key: 'note', val: this.note});
					} else if (this.type == 'invoice') {
						if (this.radio > 0) {
							this.setOrderExtra({key: 'invoiceId', val: this.radio});
							this.setOrderExtra({key: 'invoiceTitle', val: this.invoiceTitle});
						}
					}
					this.$router.replace(this.util.getUrl({path: '/pages/order/create?sid=' + this.$route.query.sid}));
				}
			}
		},
		computed: {
			...mapState([
				'orderExtra', 'mallOrderExtra'
			]),
		},
		mounted() {
			var query = this.util.parseQuery(this.$route.query);
			this.query = query;
			this.sid = query.sid
			if(!this.sid) {
				this.util.$toast('参数错误');
				return;
			}
			if(query.type) {
				this.type = query.type;
			}
			this.channel = query.channel;
			if(this.channel == 'mall') {
				if(this.mallOrderExtra[this.sid] && this.mallOrderExtra[this.sid].note) {
					this.note = this.mallOrderExtra[this.sid].note;
				}
				if(this.mallOrderExtra[this.sid] && this.mallOrderExtra[this.sid].invoiceId > 0) {
					this.radio = this.mallOrderExtra[this.sid].invoiceId;
				}
				this.sids = query.sids;
			} else {
				this.note = this.orderExtra.note || '';
				this.radio = this.orderExtra.invoiceId || 0;
			}
			this.onLoad();
		}
	}
</script>

<style>
	#order-note .content .text .van-field{
		padding: 15px;
	}
	#order-note .content textarea{
		background-color: #f5f5f5;
		height: 140px;
		border:1px solid #ECECEC;
		padding:10px;
		font-size: 16px;
	}
	#order-note .content .label-box{
		padding: 10px 15px 5px 15px;
	}
	#order-note .content .label-box .label{
		position: relative;
		display: inline-block;
		padding: 4px 10px;
		font-size:12px;
		border:1px solid #ECECEC;
		margin: 0 10px 10px 0;
		color: #333;
		line-height: 20px
	}
	#order-note .content .label-box .label .icon-close{
		position: absolute;
		right: 0;
		top: 0;
		width: 16px;
		height: 16px;
		display: flex;
		align-items: center;
		justify-content: center;
		transform: translate(50%, -50%);
		background-color: rgba(0, 0, 0, 0.3);
		color: #fff;
		border-radius: 100px;
		font-size: 12px;
	}
	#order-note .content .save-btn{
		padding: 15px;
	}
	#order-note .add-invoice{
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 15px;
		color: #ff2d4b;
		background-color: #fff;
		font-size: 14px;
	}
	#order-note .add-invoice .icon{
		font-size: 18px;
		padding-right: 5px;
	}
</style>


